import { useEffect } from 'react';
import style from '../scss/body.module.scss';
import Sidebar from '@/components/sidebar';
import { history, useArticle, useBook, useFile } from 'topwrite';
import Document from '@/components/document';

export default function Body() {

    const file = useFile();
    const { summary } = useBook();

    const article = useArticle();

    useEffect(() => {
        if (!article) {
            const firstPath = summary.getFirstPath();
            if (firstPath) {
                history.replace(file.relative(firstPath));
            }
        }
    }, [article]);

    if (!article) {
        return null;
    }

    return <div className={style.body}>
        <Sidebar />
        <Document />
    </div>;
}
